<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设备管理</el-breadcrumb-item>
      <el-breadcrumb-item>设备列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row :gutter="20">
        <el-col :span="5">
          <el-select v-model="queryInfo.type" placeholder="类型选择" clearable @clear="getDeviceList">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <el-select v-model="queryInfo.status" placeholder="状态选择" clearable @clear="getDeviceList">
            <el-option
              v-for="item in states"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <el-input placeholder="设备名称" v-model="queryInfo.name" clearable @clear="getDeviceList">
            <el-button slot="append" icon="el-icon-search"
                       @click="getDeviceList1"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">添加设备</el-button>
        </el-col>
      </el-row>

      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="设备名称" prop="name"></el-table-column>
        <el-table-column label="设备类型" prop="type"></el-table-column>
        <el-table-column label="注册信息" prop="registerInfo"></el-table-column>
        <el-table-column label="IP信息" prop="ip_info"></el-table-column>
        <el-table-column label="GPS信息" prop="gps_info"></el-table-column>
        <el-table-column label="状态" prop="status"></el-table-column>
        <el-table-column label="操作" width="170px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change=
          "handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!--添加设备-->
    <el-dialog
      title="添加设备"
      :visible.sync="addDialogVisible"
      width="30%"
      @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="心跳包" prop="heartbeat">
          <el-input v-model="addForm.heartbeat"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addDevice">确 定</el-button>
  </span>
    </el-dialog>
    <!--修改设备-->
    <el-dialog
      title="修改设备"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="editDialogClosed">
      <el-form :model="editForm" ref="editFormRef" label-width="70px">
        <el-form-item label="设备名">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editDeviceInfo()">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Devices.vue',
  data() {
    return {
      // 获取用户列表参数对象
      queryInfo: {
        name: '',
        type: '',
        status: '',
        // 当前页数
        pagenum: 1,
        pagesize: 2
      },
      userlist: [],
      total: 0,
      options: [{
        value: 'camera',
        label: '摄像机'
      }, {
        value: 'type2',
        label: '选项2'
      }],
      states: [{
        value: '2',
        label: '注册&存活'
      }, {
        value: '1',
        label: '注册&失联'
      }, {
        value: '0',
        label: '未注册&null'
      }],
      // 控制添加设备对话框的隐藏
      addDialogVisible: false,
      addForm: {
        heartbeat: ''
      },
      editDialogVisible: false,
      // 查询到的用户信息对象
      editForm: {}
    }
  },
  created() {
    this.getDeviceList()
  },
  methods: {
    async getDeviceList() {
      console.log(this.queryInfo)
      const { data: res } = await this.$http.get('/device/listDevice', { params: this.queryInfo })
      if (res.code !== 200) {
        return this.$meaasge.error('获取用户列表失败')
      }
      this.userlist = res.data
      this.total = res.totalpage
      console.log(res)
    },
    // 监听pagesize改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getDeviceList()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getDeviceList()
    },
    getDeviceList1() {
      this.queryInfo.pagenum = 1
      this.getDeviceList()
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    addDevice() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
        }
        const { data: res } = await this.$http.get('/device/heartbeat', { params: this.addForm })
        if (res.code !== 200) {
          this.$message.error(res.msg)
        }
        this.$message.success(res.msg)
        this.addDialogVisible = false
        this.getDeviceList1()
        // console.log(this.addForm)
      })
    },
    async showEditDialog(id) {
      console.log(id)
      const { data: res } = await this.$http.get('/device/' + id)
      if (res.code !== 200) {
        return this.$message.error(res.msg)
      }
      this.editForm = res.data
      console.log(res.data)
      this.editDialogVisible = true
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    async editDeviceInfo(id) {
      const { data: res } = await this.$http.put('/device/edit', this.editForm)
      if (res.code !== 200) {
        return this.$message.error(res.msg)
      }
      this.editDialogVisible = false
      this.getDeviceList()
      this.$message.success(res.msg)
    },
    async removeUserById(id) {
      // 弹框询问
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => {
        return err
      })

      // 如果用户确认删除，则返回值为字符串 confirm
      // 如果用户取消删除，则返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }

      const { data: res } = await this.$http.delete('/user/' + id)
      if (res.code !== 200) {
        return this.$message.error(res.msg)
      }
      this.$message.success(res.msg)
      this.getDeviceList1()
    }
  }
}
</script>

<style lang="less" scoped>
</style>
